import { FC } from "react";
import { Edit2 } from "react-feather";
import { StyledEditPhoto } from "./style";

interface ContactEditPhotoProps{
    ref?:any
    onClick?:any,
    type?:'button' |'submit'| 'reset'
}

const ContactEditPhoto: FC<ContactEditPhotoProps> = ({ref,onClick,type}) => {
    return (
        <StyledEditPhoto ref={ref} onClick={onClick} type={type}>
            <Edit2 />
        </StyledEditPhoto>
    );
};

export default ContactEditPhoto;
