/* eslint-disable jsx-a11y/label-has-associated-control */
import styled, { themeGet } from "@doar/shared/styled";

interface RowProps{
    md?:string | number,
    sm?:string| number,
    lg?:string| number,
    xl?:string| number,
    align?:string| number,
    justify?:string| number,
}

interface StyledWrap2Props{
    pb?:string
}


interface ContainerProps{
    size?:number
    justify?:string,
    align?:string
}
export const StyledWrap2 = styled.div<StyledWrap2Props>`
    width: 100%;
    padding-top: 20px;
    padding-bottom: ${props=>props.pb};
`;

export const StyledWrap = styled.div`
    width: 100%;
    padding-top: 20px;
`;
export const Container = styled.div<ContainerProps>`
    display: flex;
    flex-wrap: wrap;
    flex:${props => props.size};
    justify-content: ${props=>props.justify || 'unset'};
    align-items:${props=>props.align || 'unset'} ;
`;



export const InvisibleInput = styled.input`
  display: none;
`;




export const Row = styled.div<RowProps>`
  width: ${props => props.md || 50}%;
  padding-right: 10px;
  justify-content: ${props=>props.justify||'unset'};
  align-items: ${props=>props.align || 'unset'};
  @media screen and (max-width:576px){
    width: ${props => props.sm || 100}%;
  }
  @media screen and (max-width:768px) and (min-width: 576px){
    width: ${props => props.sm || 100}%;
  }
  @media screen and (max-width:992px) and (min-width: 768px){
    width: ${props => props.md || 50}%;
  }
  @media screen and (max-width:1200px) and (min-width: 992px){
    width: ${props => props.lg || 50}%;
  }
  @media screen and (min-width: 1200px){
    width: ${props => props.xl || 50}%;
  }
  
`;


interface StyledTitleProps{
    align?:string
}
export const StyledTitle = styled.h3<StyledTitleProps>`
    color: ${themeGet("colors.text")};
    margin-bottom: 5px;
    text-align: ${props=>props.align || 'unset'};
`;




export const StyledDesc = styled.p`
    font-size: 16px;
    color: ${themeGet("colors.text3")};
    margin-bottom: 40px;
`;

export const StyledLabelWrap = styled.div`
    margin-bottom: 5px;
    justify-content: space-between;
    display: flex;
`;

export const StyledDivider = styled.div`
    position: relative;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    color: ${themeGet("colors.text3")};
    font-size: 10px;
    font-weight: 500;
    font-family: ${themeGet("fonts.interui")};
    letter-spacing: 0.5px;
    margin: 15px 0;
    &:after,
    &:before {
        content: "";
        display: block;
        flex: 1;
        height: 1px;
        background-color: ${themeGet("colors.light")};
    }
    &:before {
        margin-right: 10px;
    }
    &:after {
        margin-left: 10px;
    }
`;

export const StyledBottomText = styled.div`
    margin-top: 20px;
    text-align: center;
    font-size: 13px;
`;
