import {FC, useState,Fragment} from "react";
import SignupForm from "../../components/signup-form";
import {
    StyledMedia,
    StyledMediaBody,
    StyledImage,
    StyledImgText,
    StyledSignUp, StyledSignin, StyledSignUpContainer2,
} from "./style";
import Steps from "../../components/signup-form/Steps";
import BusinessInfoForm from "../../components/signup-form/businessInfoForm";
import Confirmation from "../../components/signup-form/Confirmation";

const AuthContainer: FC = () => {
    let [step,setstep] = useState(1)
    let changeStep = ()=>{
        setstep(step+1)
    }
    return (
            <Fragment>
                <StyledSignUpContainer2>
                    <Steps current={step} />
                    {step === 1 && <StyledSignin><SignupForm handleChange={()=>changeStep()} /></StyledSignin>}
                    {step === 2 && <StyledSignin><BusinessInfoForm handleChange={()=>changeStep()} /></StyledSignin>}
                    {step === 3 && <StyledSignUp><Confirmation handleChange={()=>changeStep()} /></StyledSignUp>}
                </StyledSignUpContainer2>
            </Fragment>
    );
};

export default AuthContainer;
