import StepsContainer from "./styles";

let Steps = (props:any)=>{
        return (
        <StepsContainer >
                <li className={props.current >= 1 ? `${props.current > 1 && 'active'} current ` : ""}>Personal</li>
                <li className={props.current >= 2 ? `${props.current > 2 &&'active'} current ` : ""}>Business</li>
                <li className={props.current >= 3 ? `${props.current > 3 &&'active'} current ` : ""}>Confirmation</li>
        </StepsContainer>
        )
}
export default Steps