import {Container, Row, StyledDivider} from "../signup-form/style";
import {StyledAvatar, StyledLeft} from "@doar/components/src/apps/contacts/modal-edit/style";
import {Avatar, AvatarInitial, Button, FormGroup, Input, Label, Radio, Select, Textarea} from "@doar/components";
import ContactEditPhoto from "../apps/contacts/edit-photo-button";
import {hasKey} from "@doar/shared/dist/methods/index";
import {FC} from "react";


interface BusinessInfoForm{
    errors:any,
    register:any,
    images:any,
    setimages:any,
    branch_address:any,
    setbranch_address:any,
    fileUploadRef:any

}


let BusinessInfoFormOnly:FC<BusinessInfoForm> = ({
     errors,
     register,
     images,
     setimages,
     branch_address,
     setbranch_address,
     fileUploadRef
})=>{
    let addBranchAddress = ()=>{
        let array = [...branch_address]
        array.push({
            country:"",
            city:"",
            index:"",
            address:""
        })
        setbranch_address(array)
    }
    let handleCountryChange = (e:any,ind:any)=>{
        let branch:any = [...branch_address]
        branch[ind].country = e.target.value
        setbranch_address(branch)
    }
    let handleCityChange = (e:any,ind:any)=>{
        let branch:any = [...branch_address]
        branch[ind].city = e.target.value
        setbranch_address(branch)
    }
    let handleAddressChange = (e:any,ind:any)=>{
        let branch:any = [...branch_address]
        branch[ind].address = e.target.value
        setbranch_address(branch)
    }
    let handleZIPChange = (e:any,ind:any)=>{
        let branch:any = [...branch_address]
        branch[ind].index = e.target.value
        setbranch_address(branch)
    }
    let removeBranchAddress = (ind:number)=>{
        let array = [...branch_address]
        array.splice(ind-1,1)
        setbranch_address(array)
    }

    let handleButtonClick = ()=>{
        fileUploadRef?.current?.click()
    }
    return(
        <>
            <Container>
                <Row>
                    <StyledLeft>
                        <StyledAvatar>
                            <Avatar size='xxl'>
                                {images.length!==0 ?<img  src={URL.createObjectURL(images)}/>:
                                    <AvatarInitial bg="gray700" fontWeight={400}>
                                        A
                                    </AvatarInitial>
                                }
                            </Avatar>

                            <ContactEditPhoto type='button'  onClick={handleButtonClick} />
                            <input ref={fileUploadRef} onChange={(e:any)=>setimages(e.target.files[0])} accept='image/*' type='file' style={{display:'none'}}/>
                        </StyledAvatar>
                    </StyledLeft>
                </Row>
            </Container>
            <Container>
                <Row>
                    <FormGroup mb="20px">
                        <Label display="block" mb="5px" htmlFor="company_name">
                            Business name
                        </Label>
                        <Input
                            id="company_name"
                            type="text"
                            placeholder="Enter your company name"
                            feedbackText={errors?.company_name?.message}
                            state={
                                hasKey(errors, "company_name") ? "error" : "success"
                            }
                            showState={!!hasKey(errors, "company_name")}
                            {...register("company_name", {
                                required: "Company Name is required",
                            })}
                        />
                    </FormGroup>
                </Row>
                <Row>
                    <FormGroup mb="20px">
                        <Label display="block" mb="5px" htmlFor="legal_name">
                            Legal name
                        </Label>
                        <Input
                            id="legal_name"
                            type="text"
                            placeholder="Enter your legal name"
                            feedbackText={errors?.legal_name?.message}
                            state={
                                hasKey(errors, "legal_name") ? "error" : "success"
                            }
                            showState={!!hasKey(errors, "legal_name")}
                            {...register("legal_name", {
                                required: "Legal Name is required",
                            })}
                        />
                    </FormGroup>
                </Row>
                <Row>
                    <FormGroup mb="20px" >
                        <Label display="block" mb="5px" htmlFor="director_name">
                            Name of chairman of business
                        </Label>
                        <Input
                            id="director_name"
                            type="text"
                            placeholder="Enter name of chairman of business"
                            feedbackText={errors?.director_name?.message}
                            state={hasKey(errors, "director_name") ? "error" : "success"}
                            showState={!!hasKey(errors, "director_name")}
                            {...register("director_name", {
                                required: "director name is required"
                            })}
                        />
                    </FormGroup>
                </Row>
                <Row>
                    <FormGroup mb="15px">
                        <Label display="block" mb="20px" htmlFor="business_type">
                            Business type
                        </Label>
                        <Container>
                            <Row><Radio value='Individual' pl='1rem' mt="8px" label='Individual' id='Individual' name='business_type'/></Row>
                            <Row><Radio value='Other' label='Other' mt="8px" id='Other' name='business_type'/></Row>
                        </Container>
                    </FormGroup>
                </Row>
                <Row sm="100" md="100" lg="100" xl="100">
                    <FormGroup mb='20px'>
                        <Label display="block" mb="5px" htmlFor="description">
                            Description
                        </Label>
                        <Textarea
                            id="description"
                            feedbackText={errors?.description?.message}
                            state={hasKey(errors, "description") ? "error" : "success"}
                            showState={!!hasKey(errors, "description")}
                            {...register("description", {
                                required: "description is required",
                            })}
                        />
                    </FormGroup>
                </Row>
                <Row>
                    <FormGroup mb="20px" >
                        <Label display="block" mb="5px" htmlFor="company_address">
                            Company Address
                        </Label>
                        <Input
                            id="company_address"
                            type="text"
                            placeholder="Enter your company address"
                            feedbackText={errors?.company_address?.message}
                            state={hasKey(errors, "company_address") ? "error" : "success"}
                            showState={!!hasKey(errors, "company_address")}
                            {...register("company_address", {
                                required: "company address is required",
                            })}
                        />
                    </FormGroup>
                </Row>
                <Row>
                    <FormGroup mb="20px">
                        <Label display="block" mb="5px" htmlFor="password">
                            Website
                        </Label>
                        <Input
                            id="website"
                            type="text"
                            placeholder="Enter your website"
                            feedbackText={errors?.website?.message}
                            state={hasKey(errors, "website") ? "error" : "success"}
                            showState={!!hasKey(errors, "website")}
                            {...register("website", {
                                pattern: {
                                    value: /[(http(s)?):\/\/(www\.)?a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/i,
                                    message: "invalid url",
                                },
                            })}
                        />
                    </FormGroup>
                </Row>
                <Row>
                    <FormGroup mb="20px" >
                        <Label display="block" mb="5px" htmlFor="bank_account">
                            Bank account
                        </Label>
                        <Input
                            id="bank_account"
                            type="text"
                            placeholder="Enter your bank account"
                            feedbackText={errors?.bank_account?.message}
                            state={hasKey(errors, "bank_account") ? "error" : "success"}
                            showState={!!hasKey(errors, "bank_account")}
                            {...register("bank_account", {
                                required: "bank account is required",
                            })}
                        />
                    </FormGroup>
                </Row>
                <Row>
                    <FormGroup mb="20px" >
                        <Label display="block" mb="5px" htmlFor="inn">
                            INN
                        </Label>
                        <Input
                            id="inn"
                            type="text"
                            placeholder="Enter your INN"
                            feedbackText={errors?.inn?.message}
                            state={hasKey(errors, "inn") ? "error" : "success"}
                            showState={!!hasKey(errors, "inn")}
                            {...register("inn", {
                                required: "INN is required",
                            })}
                        />
                    </FormGroup>
                </Row>
                <Row>
                    <FormGroup mb="20px" >
                        <Label display="block" mb="5px" htmlFor="identification_number">
                            Identification number
                        </Label>
                        <Input
                            id="identification_number"
                            type="text"
                            placeholder="Enter your Identification number"
                            feedbackText={errors?.identification_number?.message}
                            state={hasKey(errors, "identification_number") ? "error" : "success"}
                            showState={!!hasKey(errors, "identification_number")}
                            {...register("identification_number", {
                                required: "Identification number is required",
                            })}
                        />
                    </FormGroup>
                </Row>
                <Row>
                    <FormGroup mb="20px" >
                        <Label display="block" mb="5px" htmlFor="nds">
                            NDS
                        </Label>
                        <Input
                            id="nds"
                            type="text"
                            placeholder="Enter your NDS"
                            feedbackText={errors?.nds?.message}
                            state={hasKey(errors, "nds") ? "error" : "success"}
                            showState={!!hasKey(errors, "nds")}
                            {...register("nds", {
                                required: "NDS is required",
                            })}
                        />
                    </FormGroup>
                </Row>

                <Row>
                    <FormGroup mb="20px" >
                        <Label display="block" mb="5px" htmlFor="oked">
                            OKED
                        </Label>
                        <Input
                            id="oked"
                            type="text"
                            placeholder="Enter your OKED"
                            feedbackText={errors?.oked?.message}
                            state={hasKey(errors, "oked") ? "error" : "success"}
                            showState={!!hasKey(errors, "oked")}
                            {...register("oked", {
                                required: "OKED is required",
                            })}
                        />
                    </FormGroup>
                </Row>
                <Row>
                    <FormGroup mb="20px" >
                        <Label display="block" mb="5px" htmlFor="delivery_options">
                            Delivery Options
                        </Label>
                        <Select
                            id='delivery_options'
                            feedbackText={errors?.delivery_options?.message}
                            state={hasKey(errors, "delivery_options") ? "error" : "success"}
                            showState={!!hasKey(errors, "delivery_options")}
                            {...register("delivery_options", {
                                required: "Delivery Options is required",
                            })}
                        >
                            <option hidden={true} value='Delivery Options is required'></option>
                        </Select>

                    </FormGroup>
                </Row>

                <Row>
                    <FormGroup mb="20px" >
                        <Label display="block" mb="5px" htmlFor="account_type">
                            Account type
                        </Label>
                        <Select
                            id='account_type'
                            feedbackText={errors?.account_type?.message}
                            state={hasKey(errors, "account_type") ? "error" : "success"}
                            showState={!!hasKey(errors, "account_type")}
                            {...register("account_type", {
                                required: "account_type is required",
                            })}
                        >
                            <option hidden={true} value='account_type is required'></option>
                        </Select>

                    </FormGroup>
                </Row>
                <Row>
                    <FormGroup mb="20px" >
                        <Label display="block" mb="5px" htmlFor="mfo_bank">
                            MFO bank

                        </Label>
                        <Input
                            id="mfo_bank"
                            type="text"
                            placeholder="Enter your MFO bank"
                            feedbackText={errors?.mfo_bank?.message}
                            state={hasKey(errors, "mfo_bank") ? "error" : "success"}
                            showState={!!hasKey(errors, "mfo_bank")}
                            {...register("mfo_bank", {
                                required: "MFO bank is required",
                            })}
                        />
                    </FormGroup>
                </Row>

            </Container>
            <StyledDivider>Branch Address</StyledDivider>

            {branch_address.map((one:any,ind:any)=>(
                <Container>
                    <Row>
                        <FormGroup mb="20px" >
                            <Label display="block" mb="5px" htmlFor="country">
                                Country
                            </Label>
                            <Select
                                name='country'
                                id='country'
                                onChange={(e)=>handleCountryChange(e,ind)}
                                feedbackText={errors?.country?.message}
                                state={hasKey(errors, "country") ? "error" : "success"}
                                showState={!!hasKey(errors, "country")}
                            >
                                <option hidden={true} value='country is required'></option>
                            </Select>
                        </FormGroup>
                    </Row>
                    <Row>
                        <FormGroup mb="20px" >
                            <Label display="block" mb="5px" htmlFor="city">
                                City
                            </Label>
                            <Select
                                name='city'
                                id='city'
                                onChange={(e)=>handleCityChange(e,ind)}
                                feedbackText={errors?.city?.message}
                                state={hasKey(errors, "city") ? "error" : "success"}
                                showState={!!hasKey(errors, "city")}
                            >
                                <option hidden={true} value='city is required'></option>
                            </Select>
                        </FormGroup>
                    </Row>
                    <Row   sm="40" md="45" lg="46" xl="46">
                        <FormGroup mb="20px" >
                            <Label display="block" mb="5px" htmlFor="zip">
                                ZIP code
                            </Label>
                            <Input
                                name="zip"
                                id="zip"
                                type="text"
                                placeholder="Enter your ZIP code"
                                onChange={(e)=>handleZIPChange(e,ind)}
                                feedbackText={errors?.index?.message}
                            />
                        </FormGroup>
                    </Row>
                    <Row sm="40" md="45" lg="46" xl="46">
                        <FormGroup mb="20px" >
                            <Label display="block" mb="5px" htmlFor="address">
                                Address
                            </Label>
                            <Input
                                name="address"
                                id="address"
                                type="text"
                                placeholder="Enter your address"
                                onChange={(e)=>handleAddressChange(e,ind)}
                            />
                        </FormGroup>
                    </Row>
                    <Row align='end' justify='space-between' sm="20" md="10" lg="8" xl="8" >
                        <Button fullwidth onClick={()=>removeBranchAddress(ind)}  color='danger' mt="20px" > Delete</Button>
                    </Row>
                </Container>
            ))}
            <Container>
                <Row sm="100" md="100" lg="100" xl="100">
                    <Button  type="button" onClick={()=>addBranchAddress()} color="danger" fullwidth>
                        Add
                    </Button>
                </Row>
                <Row sm="100" md="100" lg="100" xl="100">
                    <hr/>
                </Row>
            </Container>
        </>
    )
}
export default BusinessInfoFormOnly