import styled from "@doar/shared/dist/styled";

let StepsContainer = styled.ul`
  counter-reset: step;
  & > li{
    list-style: none;
    display: inline-block;
    width: 31%;
    position: relative;
    text-align: center;
    cursor: pointer;
  }
  & > li:before{
    content: counter(step);
    counter-increment: step;
    width: 25px;
    height: 25px;
    line-height : 25px;
    border: 1px solid #ddd;
    border-radius: 100%;
    display: block;
    text-align: center;
    margin: 0 auto 10px auto;
    background-color: #fff;
  }
  & > li:after{
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: #ddd;
    top: 15px;
    left: -50%;
    z-index : -1;
  }
  & > li.current:before{
    content: counter(step);
    counter-increment: step;
    width: 25px;
    height: 25px;
    line-height : 25px;
    border: 1px solid green;
    border-radius: 100%;
    display: block;
    text-align: center;
    margin: 0 auto 10px auto;
    background-color: #fff;
    font-size: small;
  }
  & > li.active:before{
    content: "✔";
    width: 25px;
    height: 25px;
    line-height : 25px;
    border: 1px solid green;
    border-radius: 100%;
    display: block;
    text-align: center;
    margin: 0 auto 10px auto;
    background-color: #fff;
    font-size: small;
  }
  & > li:first-child:after{
    content: none;
    color: green;
  }
  & > li.active{
    color: green;
  }
  & > li.active:before{
    color: green;
  }
  & > li.active + li:after {
    background-color: green;
  }
  
`;

export default StepsContainer
