import {FC, useRef, useState} from "react";
import {useForm} from "react-hook-form";
import {
    Container,
    Row,
    StyledBottomText,
    StyledDesc,
    StyledDivider,
    StyledTitle,
    StyledWrap
} from "./style";
import {
    Anchor,
    Button,
    FormGroup,
    Input,
    Label,
    Text,
    Radio, Textarea, Select
} from "@doar/components";
import {hasKey} from "@doar/shared/dist/methods";
import {
    Avatar,
    AvatarInitial
} from '@doar/components'
import {
    StyledLeft,
    StyledAvatar
} from '@doar/components/src/apps/contacts/modal-edit/style'
import ContactEditPhoto from "../apps/contacts/edit-photo-button";
import BusinessInfoFormOnly from "../profile_forms/businessInfoForm";




interface BusinessInfoFormProps{
    handleChange?:any
}


interface IFormValues {
    main_image: string;
    company_name: string;
    legal_name: string;
    business_name: string;
    type_business:boolean;
    description:string;
    address:string;
    director_full_name:string;
    bank_account:string;
    inn:string;
    identification_number:string;
    nds:string;
    oked:string;
    account_type:string;
    mfo_bank:string;
    delivery_type:string;
    website:string;
    branch:[{
        country:string;
        city:string;
        index:number;
        address:string;
    }]

}

let BusinessInfoForm:FC<BusinessInfoFormProps>= ({handleChange})=>{
    let fileUploadRef = useRef<HTMLInputElement>(null)
    let [images,setimages] = useState<any>([])
    const {
        register,
        handleSubmit,
        formState: { errors },
    } = useForm();
    let [branch_address,setbranch_address] = useState([{
        country:"",
        city:"",
        index:"",
        address:"",
    }])


    const onSubmit = (data: IFormValues) => {
        console.log(JSON.stringify(data, null));
        handleChange()
    };

    return (
        <StyledWrap>
            <StyledTitle>Business account</StyledTitle>

            <form action="#" onSubmit={handleSubmit(onSubmit)} noValidate>
                <BusinessInfoFormOnly
                    errors={errors}
                    register={register}
                    images={images}
                    setimages={(e:any)=>setimages(e)}
                    branch_address={branch_address}
                    setbranch_address={(e:any)=>setbranch_address(e)}
                    fileUploadRef={fileUploadRef}
                />
                <Container>

                    <Row sm="100" md="100" lg="100" xl="100">
                        <Button type="submit" color="brand2" fullwidth>
                            Next
                        </Button>
                    </Row>

                </Container>

            </form>
        </StyledWrap>
    );
}
export default BusinessInfoForm